import { Component } from '@angular/core';
import { NavController, ModalController, NavParams, ViewController, Platform, List } from 'ionic-angular';
import 'rxjs/add/operator/map';

import { Slides } from 'ionic-angular';
import {ItemPage} from '../item/item';

@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html'
})
export class MenuPage{

    posts: any;
    sectionsList: string[] = [];

    constructor(public platform: Platform,
                public params: NavParams,
                public navCtrl: NavController,
                public viewCtrl: ViewController,
                public modalCtrl: ModalController){

        this.posts = this.params.get('menuJSON');
        console.log(this.posts);
        for(let sec of this.posts){
            this.sectionsList.push(sec.section);
            console.log(sec.section);
        }
        console.log(this.sectionsList);
    }

    openSection(sectionNum) {
        console.log(sectionNum);
        var data = {sectionJSON: this.params.get('menuJSON'), sectionsList:this.sectionsList, sectionNum: sectionNum};
        console.log(data)
        // let modal = this.modalCtrl.create(ItemPage, data);
        // modal.present();
        this.navCtrl.push(ItemPage,data);
    }

    dismiss(){
        this.viewCtrl.dismiss();
    }
}




