import { Component } from '@angular/core';
import { NavController, ModalController, NavParams, ViewController, Platform  } from 'ionic-angular';
import 'rxjs/add/operator/map';

import { Slides } from 'ionic-angular';

@Component({
  selector: 'page-item',
  templateUrl: 'item.html'
})
export class ItemPage {
    allItems: any;
    items: any;
    section: any;
    sections: any;
    sectionNum: any;
    sectionJSON: any;

    constructor(public platform: Platform,
                public params: NavParams,
                public viewCtrl: ViewController,
                public navCtrl: NavController) {
                    
                this.sections = this.params.get("sectionsList");
                this.sectionNum = this.params.get("sectionNum");
                this.sectionJSON = this.params.get("sectionJSON");
                this.allItems = this.sectionJSON[this.sectionNum];
                this.items = this.allItems.items;
                this.section = this.allItems.section;
                console.log(this.sectionNum);
                console.log(this.allItems);

    }

    goToOtherSection(sectionNum) {
        var data;

        data = {sectionsList: this.sections, sectionNum: sectionNum, sectionJSON: this.sectionJSON};
        this.navCtrl.push(ItemPage, data);
    }

    dismiss(){
        this.viewCtrl.dismiss();
    }

}



